
import { defineComponent, computed, ref, onMounted, SetupContext } from "vue";
import { IMessageTemplate, IOrder, ISignup, ISubscription } from '@/types/Interfaces';
import useSubscription from "@/types/Subscription";

export default defineComponent({
  components: {
    
  },
  props: {
    subscription: {
      type: Object as () => ISubscription[]
    }
  },
  emits: [
    'close',
  ],
  setup(props: any, context: SetupContext) {
    const {
      getSubscription
    } = useSubscription('doctor');

    const subscStatus = {
      0: '継続中',
      1: '休止中',
      2: '解約'
    }
    const payStatus = {
      0: '支払い未処理',
      1: '支払い作成成功 未確定',
      2: '支払処理確定'
    }

    const payMethods = {
      0: "クレジットカード決済",
      1: "代金引換",
      2: "Paidy後払い",
      3: "NP後払い",
      4: "銀行振込"
    }
    //  const subscStatus = {
    //   0: '継続中',
    //   1: '休止中',
    //   2: '解約'
    // }
    const delivTimes = {
      0: "指定なし",
      1: "午前中",
      12: "12:00～14:00",
      14: "14:00～16:00",
      16: "16:00～18:00",
      18: "18:00～20:00",
      19: "19:00～21:00",
    }
    const orderStatus = {
      0: "キャンセル",
      1: "新規受付",
      2: "入金待ち",
      3: "取り寄せ中",
      4: "発送待ち",
      5: "発送済み",
    }
    const onClose = () => {
      context.emit('close')  
    };
    const data = ref<ISubscription | null>(null);
    const userId = computed(() => {
      return props.prescript.doctor.id
    })
    const loading = ref(true);
    onMounted(async () => {
      // loading.value = true;
      data.value = await getSubscription(props.subscription.id);
      loading.value = false
    })
    return {
      onClose,
      userId,
      payStatus,
      subscStatus,
      payMethods,
      delivTimes,
      orderStatus,
      data,
      loading
    }
  }
})
